.ar7_footer {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}
.ar7_footer_sub_2 {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media screen and (min-width: 0px) and (max-width: 481px) {
  .ar7_footer_sub_2 {
    width: 100%;
    transform: scale(0.7);
  }
}
