.ar7_product_card_1 {
  border: 2px solid black;
  box-shadow: 5px 5px 5px black;
  overflow: hidden;
  border-radius: 5px;
  transition: transform 0.5s;
  width: 100%;
}
.ar7_product_card_1:hover {
  transform: translateY(-10px);
}
.ar7_product_sub_box {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30rem;
}
.ar7_product_box {
  display: flex;
  align-items: center;
  justify-content: center;
}
.ar7_product_images_2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.ar7_form_div {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 5rem;
  width: 100%;
  flex-wrap: wrap;
}

.ar7_form_div_inside_2 {
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 50rem;
}
.ar7_text_div_form {
  max-width: 60rem;
  margin: 3rem;
}
@media screen and (min-width: 0px) and (max-width: 493px) {
  .ar7_single_image_2 {
    width: 11rem;
    height: 11rem;
  }
  .ar7_text_box {
    font-size: 1.8rem;
    margin: 1.5rem;
  }
}
@media screen and (min-width: 0px) and (max-width: 991px) {
  .ar7_texts_div {
    margin-top: 3rem;
  }
}
